'use strict';
// Ð¿Ð¾Ð»ÑÑÐ°ÐµÐ¼ ÑÐ»ÐµÐ¼ÐµÐ½ÑÑ Ð¸Ð½ÑÐµÑÑÐµÐ¹ÑÐ°
const refs = {
  form: document.querySelector('#form'),
  formBtnStart: form.querySelector('#form-button-start'),
  formBtnStop: form.querySelector('#form-button-stop'),
  input: form.querySelector('#date-input'),
};

class CountdownTimer {
  constructor({ selector, targetDate }) {
    this.selector = selector;
    this.targetDate = targetDate;
  }

  // Ð¿ÐµÑÐµÐ²Ð¾Ð´Ð¸Ð¼ Ð²Ð²ÐµÐ´ÐµÐ½Ð½ÑÑ Ð´Ð°ÑÑ Ð² Ð¼Ñ
  getTargetDate() {
    return this.targetDate.getTime();
  }

  //  Ð¿Ð¾Ð»ÑÑÐ°ÐµÐ¼ ÑÐµÐºÑÑÑÑ Ð´Ð°ÑÑ Ð² Ð¼Ñ
  getCurrentDate() {
    return Date.now();
  }

  // Ð·Ð°Ð¿ÑÑÐº ÑÐ°Ð¹Ð¼ÐµÑÐ°
  startTimer() {
    // ÐÑÐ¾Ð²ÐµÑÑÐµÐ¼ ÑÑÐ¾Ð± Ð´Ð°ÑÐ° Ð±ÑÐ»Ð° Ð±Ð¾Ð»ÑÑÐµ ÑÐµÐºÑÑÐµÐ¹
    const passed = this.getTargetDate() - this.getCurrentDate() <= 0;
    if (passed) {
      console.log('ÐÐ°Ð´Ð°Ð½Ð¾Ðµ Ð²ÑÐµÐ¼Ñ Ð½Ð°ÑÑÑÐ¿Ð¸Ð»Ð¾!');
      return;
    }

    // ÑÐ°ÑÑÑÐ¸ÑÑÐ²Ð°ÐµÐ¼ Ð²ÑÐµÐ¼Ñ
    this.timerId = setInterval(() => {
      const time = this.getTargetDate() - this.getCurrentDate();

      const days = Math.floor(time / (1000 * 60 * 60 * 24));
      const hours = String(
        Math.floor((time % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
      ).padStart(2, '0');
      const mins = String(
        Math.floor((time % (1000 * 60 * 60)) / (1000 * 60)),
      ).padStart(2, '0');
      const secs = String(Math.floor((time % (1000 * 60)) / 1000)).padStart(
        2,
        '0',
      );

      // Ð¾Ð±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ Ð¸Ð½ÑÐµÑÑÐµÐ¹Ñ ÑÐ°Ð¹Ð¼ÐµÑÐ°
      updateClockface(this.selector, days, hours, mins, secs);

      // ÐºÐ¾Ð³Ð´Ð° Ð²ÑÐµÐ¼Ñ Ð¸ÑÑÐµÐºÐ»Ð¾, Ð¾ÑÑÐ°Ð½Ð°Ð²Ð»Ð¸Ð²Ð°ÐµÐ¼ ÑÐ°Ð¹Ð¼ÐµÑ
      if (time <= 0) {
        this.stopTimer();
        console.log('ÐÐ°Ð´Ð°Ð½Ð¾Ðµ Ð²ÑÐµÐ¼Ñ Ð½Ð°ÑÑÑÐ¿Ð¸Ð»Ð¾!');
        return;
      }
    }, 1000);

    // ÐµÑÐ»Ð¸ ÑÐ°Ð¹Ð¼ÐµÑ Ð°ÐºÑÐ¸Ð²ÐµÐ½, Ð·Ð°Ð¿ÑÐµÑÐ°ÐµÐ¼ Ð²Ð²Ð¾Ð´ Ð´Ð°ÑÑ Ð¸ Ð·Ð°Ð¿ÑÑÐº ÑÐ°Ð¹Ð¼ÐµÑÐ°
    refs.formBtnStart.disabled = true;
    refs.input.disabled = true;
    refs.input.value = '';
    refs.formBtnStart.classList.add('btnDisabled');
  }

  // ÑÐ±ÑÐ¾Ñ ÑÐ°Ð¹Ð¼ÐµÑÐ°
  stopTimer() {
    clearInterval(this.timerId);
    updateClockface(this.selector, '00', '00', '00', '00');

    // ÑÐ°Ð·ÑÐµÑÐ°ÐµÐ¼ Ð²Ð²Ð¾Ð´ Ð´Ð°ÑÑ Ð¸ Ð·Ð°Ð¿ÑÑÐº ÑÐ°Ð¹Ð¼ÐµÑÐ°
    refs.formBtnStart.disabled = false;
    refs.input.disabled = false;
    refs.formBtnStart.classList.remove('btnDisabled');
  }
}

// Ð¾ÑÑÐ¸ÑÐ¾Ð²ÐºÐ° ÑÐ°Ð¹Ð¼ÐµÑÐ° Ð² Ð¸Ð½ÑÐµÑÑÐµÐ¹ÑÐµ
function updateClockface(selector, days, hours, mins, secs) {
  document.querySelector(
    `${selector} span[data-value="days"]`,
  ).innerHTML = days;
  document.querySelector(
    `${selector} span[data-value="hours"]`,
  ).innerHTML = hours;
  document.querySelector(
    `${selector} span[data-value="mins"]`,
  ).innerHTML = mins;
  document.querySelector(
    `${selector} span[data-value="secs"]`,
  ).innerHTML = secs;
}

//  Ð¿Ð¾Ð»ÑÑÐ°ÐµÐ¼ Ð²Ð²ÐµÐ´ÑÐ½Ð½ÑÑ Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»ÐµÐ¼ Ð´Ð°ÑÑ
let userDate;

refs.input.addEventListener('change', () => {
  userDate = refs.input.value;
});

// ÑÐ¾Ð·Ð´Ð°ÐµÐ¼ Ð¸ Ð·Ð°Ð¿ÑÑÐºÐ°ÐµÐ¼ ÑÐ°Ð¹Ð¼ÐµÑ Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑ Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑ start
refs.formBtnStart.addEventListener('click', () => {
  // ÑÐ¾Ð·Ð´Ð°ÐµÐ¼ ÑÐ°Ð¹Ð¼ÐµÑ Ñ Ð¿ÐµÑÐµÐ´Ð°Ð½Ð½Ð¾Ð¹ Ð´Ð°ÑÐ¾Ð¹
  const timer = new CountdownTimer({
    selector: '#timer-1',
    targetDate: new Date(userDate),
  });

  timer.startTimer.call(timer);

  //ÑÐ±ÑÐ°ÑÑÐ²Ð°ÐµÐ¼ ÑÐ°Ð¹Ð¼ÐµÑ Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑ Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑ stop
  refs.formBtnStop.addEventListener('click', timer.stopTimer.bind(timer));
});
